/* eslint-disable */
export default {
  auth_request(state) {
    state.status = "loading";
  },
  auth_success(state, { token, user }) {
    state.status = "success";
    state.token = token;
    state.user = user;
  },
  auth_error(state) {
    state.status = "error";
  },
  logout(state) {
    state.status = "";
    state.token = "";
    state.user = {};
  },
  toggleSnackBar(state, { type, text }) {
    state.snackbarActive = !state.snackbarActive;
    state.snackbarText = text;
    state.snackbarType = type;
  },
};
